import Head from "next/head";
import { useRouter } from "next/router";
import React, { useState, useEffect } from "react";
import { useSelector } from "react-redux";

const CTitle = ({ secondTitle = "" }) => {
  const { title } = useSelector((state) => state.title);
  const router = useRouter();
  const [pageTitle, setPageTitle] = useState("");

  useEffect(() => {
    let path = router.pathname.slice(1);
    path = path.replaceAll("/[id]", "");
    path = path.replaceAll("/", " > ");
    path = path.replaceAll("-", " ");
    let toArray = path.split(" ");
    var toCapitalize = "Dashboard";
    if (path !== "") {
      toCapitalize = toArray
        .map((word) => {
          return word[0].toUpperCase() + word.substring(1);
        })
        .join(" ");
    }
    setPageTitle(toCapitalize + secondTitle);
  }, []);

  return (
    <Head>
      <meta property="og:site_name" content={title} />
      <title>{pageTitle === "" ? title : pageTitle + " | " + title}</title>
      <meta
        property="og:title"
        content={pageTitle === "" ? title : pageTitle + " | " + title}
      />
    </Head>
  );
};
export default CTitle;
